/*属性变量*/
.warning-warp.conter .conter-middle-reset .middle-top {
  position: relative;
}
.warning-warp.conter .conter-middle-reset .middle-top .update-range-time {
  text-align: center;
}
.warning-warp.conter .conter-middle-reset .middle-top .update-range-time i {
  float: right;
  margin-right: .49rem;
}
.warning-warp.conter .conter-middle-reset .middle-top .iconlujing1 {
  position: absolute;
  right: .4rem;
}
.warning-warp.conter .conter-middle-reset .analysis-time {
  justify-content: center;
  width: 3.6rem;
  display: flex;
  align-items: center;
  margin: 0 auto .1rem;
}
.warning-warp.conter .conter-middle-reset .analysis-time ul {
  flex: 1;
}
.warning-warp.conter .conter-middle-reset .analysis-time ul li {
  padding: 0 0.1rem;
  display: inline-block;
  margin: 0 0.04rem;
  cursor: pointer;
}
.warning-warp.conter .conter-middle-reset .analysis-time ul li.active {
  color: #52D4E0;
  border-radius: 50px;
  border: 1px solid #52D4E0;
}
.warning-warp.conter .conter-middle-reset .analysis-time ul li:last-child {
  margin: 0;
}
.warning-warp.conter .conter-middle-reset .coner-wrap .wrap-top {
  height: 100%;
  position: relative;
}
.warning-warp.conter .conter-middle-reset .coner-wrap .wrap-top .comTitleBox {
  justify-content: space-between;
}
.warning-warp.conter .conter-middle-reset .coner-wrap .wrap-top .comTitleBox .modulSelect {
  padding: 0 0.6rem;
}
.warning-warp.conter .conter-middle-reset .coner-wrap .wrap-top .comTitleBox .modulSelect .el-select {
  width: 1.2rem;
  height: 0.51rem;
  border-radius: 3px;
}
.warning-warp.conter .conter-middle-reset .coner-wrap .wrap-top .comTitleBox .modulSelect .el-select .el-input input {
  background: #1B418D;
  border-color: #1B418D;
}
.warning-warp.conter .conter-middle-reset .coner-wrap .wrap-top .sort-list {
  padding: 0 0rem 0 0.05rem;
  height: calc(100% - .9rem);
}
.warning-warp.conter .conter-middle-reset .coner-wrap .wrap-top .sort-list .content {
  width: calc(99.64%);
}
.warning-warp.conter .warncharts {
  height: calc(100% - .78rem);
  box-shadow: 0 0 black;
}
.number-total {
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 0.01rem solid #5AB0F3;
  border-bottom: 0.01rem solid #5AB0F3;
  padding: 0.25rem 0;
  margin: 0 auto;
}
.number-total dl {
  flex: 1;
  text-align: center;
  border-right: .01rem solid #ffffff;
}
.number-total dl:last-child {
  border-right-color: transparent;
}
.number-total dl dt {
  font-size: .44rem;
  color: #F3Af1B;
}
/*订阅监控-专题--strat*/
.subscription.conter .update-range-time {
  text-align: center;
  margin-bottom: .25rem;
}
.subscription.conter .update-range-time i {
  float: right;
  margin-right: .49rem;
}
.conter.monitoring .new-msg {
  height: calc(100% - .78rem);
  padding: 0;
}
/*订阅监控-专题--end*/
/*订阅监控-总览--strat*/
.conter.subscription-overview .conter-left {
  background: none;
  padding: 0;
}
.conter.subscription-overview .conter-left .conterLeft-top {
  height: 25%;
  background: url(../images/bg-left-top.png) no-repeat;
  background-size: 100% 100%;
  padding: 0.24rem;
}
.conter.subscription-overview .conter-left .conterLeft-top .overview.number-total {
  border-top: none;
  border-bottom: none;
  padding: 0.18rem 0;
}
.conter.subscription-overview .conter-left .conterLeft-top .overview.number-total dl {
  border-right: none;
}
.conter.subscription-overview .conter-left .conterLeft-top .overview.number-total dl dt {
  font-size: 0.26rem;
}
.conter.subscription-overview .conter-left .conterLeft-center {
  height: 40%;
  background: url(../images/bg-left-center.png) no-repeat;
  background-size: 100% 100%;
  padding: 0.24rem;
}
.conter.subscription-overview .conter-left .conterLeft-bottom {
  height: 36%;
  background: url(../images/bg-left-bottom.png) no-repeat;
  background-size: 100% 100%;
  padding: 0.24rem;
}
.conter.subscription-overview .conter-left .conterLeft-bottom .media-area {
  display: flex;
  align-items: center;
  justify-content: center;
}
.conter.subscription-overview .conter-left .conterLeft-bottom .media-area ul {
  width: 50%;
  padding: 0.28rem;
}
.conter.subscription-overview .conter-left .conterLeft-bottom .media-area ul:first-child {
  padding: 0 0.05rem 0.04rem 0.1rem;
}
.conter.subscription-overview .conter-left .conterLeft-bottom .media-area ul:last-child {
  padding: 0 0.1rem 0.04rem 0.05rem;
}
.conter.subscription-overview .conter-left .conterLeft-bottom .media-area ul li {
  height: .43rem;
  text-align: center;
  line-height: .43rem;
}
.conter.subscription-overview .conter-left .conterLeft-bottom .media-area ul li.head-title {
  color: #F3Af1B;
}
.conter.subscription-overview .conter-left .conterLeft-bottom .media-area ul li:nth-child(even) {
  background-color: #27549D;
}
.conter.subscription-overview .conter-left .conterLeft-bottom .media-area ul li span {
  width: 33.33%;
  display: inline-block;
}
.conter.subscription-overview .conter-left .conterLeft-bottom .media-area ul li span.blue-name {
  color: #52D4E0;
}
.conter.subscription-overview .conter-middle-reset.conter-middle .coner-wrap .wrap-bottom {
  height: 50%;
}
.conter.subscription-overview .conter-middle-reset.conter-middle .coner-wrap .wrap-bottom .flatAngle {
  width: 100%;
}
.conter.subscription-overview .conter-middle-reset.conter-middle .coner-wrap .wrap-top {
  height: 50%;
}
/*订阅监控-总览--end*/
